.contacts__title {
  margin: 0 0 50px;
  font-weight: 700;
  font-size: 45px;
  line-height: 46px;
  text-transform: uppercase;

  @include vp-1023 {
    font-size: 36px;
    line-height: 40px;
  }

  @include vp-767 {
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 30px;
  }
}

.contacts__list {
  position: relative;
  padding: 0;
  margin: 0;
  list-style: none;
  column-count: 2;
  column-gap: 100px;
  margin-bottom: 100px;

  &::after {
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    left: 49%;
    width: 2px;
    background-color: $color-default-white;
  }

  @include vp-1023 {
    margin-bottom: 70px;

    &::after {
      left: 50%;
    }
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
    gap: 20px;

    &::after {
      display: none;
    }
  }
}

.contacts__item {
  margin-bottom: 30px;

  @include vp-767 {
    margin-bottom: 0;
  }
}

.contacts__link {
  display: inline-flex;
  align-items: center;

  @include hover-focus {
    opacity: 0.6;
  }

  @include focus {
    outline: 2px solid $color-torch-red;
    border-radius: 2px;
  }
}

.contacts__link svg {
  @include vp-1023 {
    width: 22px;
    height: 22px;
  }

  @include vp-767 {
    width: 18px;
    height: 18px;
  }
}

.contacts__link-info {
  margin-left: 20px;
  color: $color-default-white;
  font-weight: 500;
  font-size: 22px;
  line-height: 28px;

  @include vp-1023 {
    margin-left: 10px;
    font-size: 18px;
    line-height: 22px;
  }


  @include vp-767 {
    font-size: 15px;
    line-height: 18px;
  }
}
