.slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.slider__item {
  max-width: 385px;
}

.slider__link {
  cursor: pointer;
  outline: none;
}

.slider__link:hover .slider__description,
.slider__link:focus .slider__description {
  color: #302683;
  text-decoration: underline;
  outline: none;
}

.slider__image {
  max-width: none;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .slider__image {
    width: 100%;
    min-width: 288px;
    min-height: 404px;
  }
}

.slider__description {
  margin: 0 0 32px;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  color: #f9fbfd;
  text-align: center;
}
@media (max-width: 767px) {
  .slider__description {
    font-size: 15px;
    line-height: 18px;
    margin-bottom: 20px;
  }
}

.slider__slider-control {
  position: absolute;
  top: 280px;
  left: 0;
  right: 0;
}
@media (max-width: 767px) {
  .slider__slider-control {
    top: 44%;
  }
}

.slider-control {
  display: flex;
  justify-content: space-between;
}

.slider-control__button {
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  width: 44px;
}
@media (hover: hover) {
  .slider-control__button:hover:not(.focus-visible) {
    background-color: white;
  }
}
.slider-control__button.focus-visible:focus {
  background-color: white;
  outline: 2px solid #ff0054;
}

.slider-control__button:hover svg,
.slider-control__button:focus svg {
  fill: #ff0054;
}

.slider-control__button--next svg {
  transform: rotate(90deg);
}

.slider-control__button--prev svg {
  transform: rotate(270deg);
}

.swiper-button-next::after,
.swiper-button-prev::after {
  display: none;
}

.swiper-button-next {
  right: 10px;
}

.swiper-button-prev {
  left: 10px;
}

.is-nojs .slider-control__button {
  display: none;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  padding: 0;
  margin: 0;
  background-color: #ffffff;
  border: none;
  outline: none;
  cursor: pointer;
  opacity: 1;
}
.swiper-pagination-bullet:hover, .swiper-pagination-bullet:focus {
  opacity: 0.6;
}
.swiper-pagination-bullet-active {
  background-color: #ff0054;
}
.swiper-pagination-bullet:disabled {
  opacity: 0.4;
}
.swiper-pagination-bullet:active {
  background-color: #ff0054;
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.site-card,
.is-tile .site-card {
  background-color: #f8f8fc;
  padding: 20px;
  display: grid;
  grid-template-rows: repeat(6, max-content);
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  align-content: space-between;
}
@media (max-width: 1023px) {
  .site-card,
.is-tile .site-card {
    gap: 10px;
    max-width: 560px;
  }
}
@media (max-width: 767px) {
  .site-card,
.is-tile .site-card {
    padding: 10px;
    gap: 8px;
    max-width: 540px;
  }
}
.site-card:focus-within,
.is-tile .site-card:focus-within {
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.15);
}
@media (hover: hover) {
  .site-card:hover:not(.focus-visible),
.is-tile .site-card:hover:not(.focus-visible) {
    box-shadow: 0 5px 25px rgba(0, 0, 0, 0.15);
    outline: none;
  }
}
.site-card.focus-visible:focus,
.is-tile .site-card.focus-visible:focus {
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.15);
  outline: none;
}

.site-card__img-link {
  grid-area: 1/1/2/-1;
}
.site-card__img-link.focus-visible:focus {
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.15);
  outline: 2px solid #ffffff;
  outline-offset: -4px;
}

.site-card__img {
  object-fit: cover;
}

.site-card__title {
  margin: 0;
  font-weight: 600;
  font-size: 22px;
  line-height: 26px;
  grid-area: 2/1/3/-1;
}
@media (max-width: 1023px) {
  .site-card__title {
    font-size: 20px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .site-card__title {
    font-size: 18px;
    line-height: 20px;
  }
}

.site-card__description {
  margin: 0;
  font-size: 18px;
  grid-area: 3/1/4/-1;
}
@media (max-width: 1023px) {
  .site-card__description {
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  .site-card__description {
    font-size: 14px;
    line-height: 16px;
  }
}

.site-card__description span {
  font-weight: 600;
}

.site-card__list-title {
  margin: 0;
  font-weight: 600;
  font-size: 22px;
  line-height: 32px;
}
@media (max-width: 1023px) {
  .site-card__list-title {
    font-size: 18px;
    line-height: 26px;
  }
}
@media (max-width: 767px) {
  .site-card__list-title {
    font-size: 14px;
    line-height: 18px;
  }
}

.site-card__list--tech {
  grid-area: 5/1/6/2;
}

.site-card__link-site {
  grid-area: 6/1/7/2;
}

.site-card__link-git {
  grid-area: 6/2/7/-1;
}

.is-list .site-card {
  grid-template-rows: repeat(6, max-content);
  grid-template-columns: min-content 1fr 1fr;
  gap: 10px 20px;
}
@media (max-width: 1023px) {
  .is-list .site-card {
    max-width: none;
  }
}

.is-list .site-card__img-link {
  display: flex;
  align-items: center;
  grid-area: 1/1/6/2;
}

.is-list .site-card__img {
  max-width: 340px;
}

.is-list .site-card__title {
  margin: auto;
  text-align: center;
  grid-area: 6/1/7/2;
}

.is-list .site-card__description {
  grid-area: 1/2/2/-1;
}

.is-list .site-card__list-title--tech {
  grid-area: 2/2/3/-1;
}
.is-list .site-card__list-title--features {
  grid-area: 4/2/5/-1;
}

.is-list .site-card__list--tech {
  grid-area: 3/2/4/-1;
}
.is-list .site-card__list--features {
  grid-area: 5/2/6/-1;
}

.is-list .site-card__link-site {
  grid-area: 6/2/7/3;
}

.is-list .site-card__link-git {
  grid-area: 6/3/7/-1;
}

.footer {
  background: linear-gradient(90deg, #ad6dcb, #b8adeb, #a7b0ec);
  min-width: 320px;
  color: #ffffff;
}

.footer__wrapper {
  padding-top: 80px;
  padding-bottom: 40px;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    padding-top: 50px;
    padding-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    padding-top: 30px;
    padding-bottom: 15px;
  }
}

.contacts__title {
  margin: 0 0 50px;
  font-weight: 700;
  font-size: 45px;
  line-height: 46px;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .contacts__title {
    font-size: 36px;
    line-height: 40px;
  }
}
@media (max-width: 767px) {
  .contacts__title {
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 30px;
  }
}

.contacts__list {
  position: relative;
  padding: 0;
  margin: 0;
  list-style: none;
  column-count: 2;
  column-gap: 100px;
  margin-bottom: 100px;
}
.contacts__list::after {
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  left: 49%;
  width: 2px;
  background-color: #ffffff;
}
@media (max-width: 1023px) {
  .contacts__list {
    margin-bottom: 70px;
  }
  .contacts__list::after {
    left: 50%;
  }
}
@media (max-width: 767px) {
  .contacts__list {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
    gap: 20px;
  }
  .contacts__list::after {
    display: none;
  }
}

.contacts__item {
  margin-bottom: 30px;
}
@media (max-width: 767px) {
  .contacts__item {
    margin-bottom: 0;
  }
}

.contacts__link {
  display: inline-flex;
  align-items: center;
}
@media (hover: hover) {
  .contacts__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.contacts__link.focus-visible:focus {
  opacity: 0.6;
}
.contacts__link.focus-visible:focus {
  outline: 2px solid #ff0054;
  border-radius: 2px;
}

@media (max-width: 1023px) {
  .contacts__link svg {
    width: 22px;
    height: 22px;
  }
}
@media (max-width: 767px) {
  .contacts__link svg {
    width: 18px;
    height: 18px;
  }
}

.contacts__link-info {
  margin-left: 20px;
  color: #ffffff;
  font-weight: 500;
  font-size: 22px;
  line-height: 28px;
}
@media (max-width: 1023px) {
  .contacts__link-info {
    margin-left: 10px;
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .contacts__link-info {
    font-size: 15px;
    line-height: 18px;
  }
}

.sort-list {
  display: flex;
  margin: 0 0 30px;
  padding: 0;
  list-style: none;
  justify-content: right;
  gap: 15px;
}
@media (max-width: 767px) {
  .sort-list {
    display: none;
  }
}

.sort-list__button {
  border: none;
  width: 41px;
  height: 31px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f8f8fc;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
}
@media (hover: hover) {
  .sort-list__button:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}

.sort-list__button svg {
  fill: #ad6dcb;
  opacity: 0.6;
}

.sort-list__button.active svg {
  opacity: 1;
}

.skill-list {
  margin: 0 0 20px;
  padding: 0;
  list-style-type: none;
}
.skill-list--modified {
  margin: 0;
  min-height: 145px;
}
@media (max-width: 767px) {
  .skill-list--modified {
    min-height: 0;
  }
}

.skill-list__item {
  padding-left: 30px;
  font-weight: 400;
  font-size: 18px;
  line-height: 20px;
  position: relative;
}
.skill-list__item::after {
  content: "";
  position: absolute;
  top: 40%;
  left: 13px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #ff0054;
}
.skill-list__item:not(:last-child) {
  margin-bottom: 5px;
}
@media (max-width: 1023px) {
  .skill-list__item--modified {
    font-size: 16px;
    line-height: 18px;
  }
}
@media (max-width: 767px) {
  .skill-list__item--modified {
    padding-left: 20px;
    font-size: 12px;
    line-height: 15px;
  }
}

.is-list .skill-list {
  min-height: 0;
  display: flex;
  flex-wrap: wrap;
}

.is-list .skill-list__item {
  margin: 0;
}

